<script setup>
  import { computed,defineProps, defineEmits,ref, watch} from 'vue'

  const props = defineProps({
    label: String,
    selectedData : [String, Number, Object],
    data: {
      type: Array,
      default: false
    },
    error: String,

    mandatory: {
      type: Boolean,
      default: false
    },
    w_label: {
      type: String,
      default: ""
    },
    w_value: {
      type: String,
      default: ""
    },
    w: {
      type: String,
      default: ""
    },
    valueByKey: {
      type: Boolean,
      default: false
    },

    disabled: {
      type: Boolean,
      default: false
    },

    alignHozirontal: {
      type: Boolean,
      default: false
    }
  })


  const emit = defineEmits(['update:selectedData'])

  const selectedData = ref(props.selectedData);

  const isDropdownVisible = ref(false);

  const labelText = computed({
    get() {      
      if(props.valueByKey) {
        if(props.data && props.data.length > 0){
          if(selectedData.value){
            var dtt = props.data.find(f => {
              return f.key == selectedData.value;
            });

            if(dtt)
              return dtt.label;
            else 
              return " - Key Not Found -"
          } else {
            return "- Select -";
          }
        } else {
          return " - No Data - "
        }
      } else {
        return selectedData.value?? "- Select -";
      }
    }
  })


  watch(() => props.selectedData, (first, second) => {
    selectedData.value = props.selectedData;
  });


  const comboSelected = (comboData)=>{
    if(props.valueByKey) 
      selectedData.value = comboData.key;
    else
      selectedData.value = comboData;
    
    emit("update:selectedData",selectedData.value);
    emit('onChange', selectedData.value);
    isDropdownVisible.value = false;
  }

  const buttonClick = () => {
    isDropdownVisible.value = !isDropdownVisible.value
    
  }

  defineExpose({
    
  });
</script>

<template>
  <div :style='[(w==""?"":"width:"+w)]' :class='["flex w-auto mb-2", (alignHozirontal?"flex-row justify-start items-center":"flex-col")]' >

    <label :style='[w_label==""?"":("width:" + w_label)]'  class="block text-sm font-medium text-white">{{label}}</label>

    <div>
      <button @click="buttonClick"
      :class='[disabled?"bg-inputDisable":"bg-inputBg","text-inputText hover:bg-inputHover border focus:bg-inputHover disabled:bg-inputBgDisable disabled:text-inputText text-sm rounded-md truncate inline-flex items-center justify-end px-3 py-2", (alignHozirontal ? "" : "w-full")]'              
              type="button">{{labelText}}
              <div class="grow"></div>
          <svg class="w-2.5 h-2.5 ms-3 ml-2" xmlns="http://www.w3.org/2000/svg" 
              fill="none" viewBox="0 0 10 6"><path stroke="currentColor" stroke-linecap="round" 
              stroke-linejoin="round" stroke-width="2" d="m1 1 4 4 4-4"/></svg>
      </button>

      <div v-show="isDropdownVisible"  aria-labelledby="dropdownHoverButton"
          class="absolute min-w-80 z-30 divide-y divide-gray-100 rounded-lg shadow mt-2 bg-gray-600 border-gray-200">
        <ul class="py-2 text-sm text-gray-200" aria-labelledby="dropdownDefaultButton">
          <li v-for="comboData in data" @click="comboSelected(comboData)">
            <a class="block px-4 py-2 hover:bg-gray-900 hover:text-white">{{(typeof comboData === 'object')?comboData.label:comboData}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>