<script setup>
    import { ref,watch, onMounted, defineProps, onUnmounted } from 'vue'

    const emit = defineEmits(['onOk','onCancel','onHide','onShow'])

    const props = defineProps({
      title: String,
      lblOk: {
        type: String,
        default: ""
      },

      lblCancel: {
        type: String,
        default: ""
      },

      message : {
        type: String,
        default:""
      },
      
      headerColor: {
        type: String,
        default: "blue"
      },

      visible: {
        type: Boolean,
        default: false
      }
    })

    const isVisible = ref(false);

    const onOk = (data) => {
      emit("onOk");
      isVisible.value = false;
    };

    const onCancel = (data) => {
      emit("onCancel");
      isVisible.value = false;
    };

    const hide = () => {
      emit("onHide");
      isVisible.value = false;
    };

    const show = () => {
      emit("onShow");
      isVisible.value = true;
    };

    defineExpose({
      hide, show
    });
</script>

<template>
    <div id="defaultmodal" aria-hidden="true" v-if="isVisible"
        class="z-50 flex items-center justify-center w-screen h-screen">
        <div class="relative rounded-lg drop-shadow-2xl bg-gray-700 " style="min-width: 30%;">
            <!-- Modal header -->
            <div :class="['bg-' + headerColor + '-800', 'flex items-center justify-between px-4 py-2 border-b rounded-t border-gray-500']">
                <h3 class="text-xl font-semibold text-white">
                    {{ title }}
                </h3>
                <button type="button" @click="hide" 
                    class="bg-transparent rounded-lg text-white text-sm w-8 h-8 ms-auto inline-flex justify-center items-center hover:bg-gray-500">
                    <svg class="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none"
                        viewBox="0 0 14 14">
                        <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                            d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6" />
                    </svg>
                    <span class="sr-only">Close modal</span>
                </button>
            </div>
            <!-- Modal body -->
            <div class="p-4 md:p-5 space-y-4 "> 
                <p class="text-base leading-relaxed text-white">
                  <span v-html="message"></span>
                </p>
            </div>
            <!-- Modal footer -->
            <div v-show="lblOk || lblCancel"
                class="flex items-center gap-2 px-4 py-3  border-t rounded-b-lg border-gray-600 mt-5">
                <button @click="onOk"  v-show="lblOk"
                    class="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 focus:ring-blue-800">{{ lblOk }}</button>
                <button @click="onCancel" v-show="lblCancel" 
                    class="text-gray-500 bg-white hover:bg-gray-100 focus:ring-4 focus:outline-none focus:ring-blue-300 rounded-lg border border-gray-200 text-sm font-medium px-5 py-2.5 hover:text-gray-900 focus:z-10 dark:bg-gray-700 dark:text-gray-300 dark:border-gray-500 dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-gray-600">{{ lblCancel }}</button>
            </div>
        </div>
    </div>
</template>