<script setup>
import { computed,defineProps, defineEmits, ref,onMounted  } from 'vue'
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import "flatpickr/dist/themes/dark.css";
import moment from 'moment';


  const props = defineProps({
    label: String,
    value: String,
    error: String,

    mandatory: {
      type: Boolean,
      default: false
    },
    isNumber: {
      type: Boolean,
      default: false
    },
    w_label: {
      type: String,
      default: ""
    },

    w: {
      type: String,
      default: ""
    },

    disabled: {
      type: Boolean,
      default: false
    },
    enableTime: {
      type: Boolean,
      default: false
    },
    endOfDayTime: {
      type: Boolean,
      default: false
    },
    alignHozirontal: {
      type: Boolean,
      default: false
    }
  })


  const emit = defineEmits(['update:value,onChange'])
  const config = ref({
    enableTime:props.enableTime,
    time_24hr:true,
    dateFormat:props.enableTime?"Y-m-dTH:i":"Y-m-d", //2015-03-25T12:00:00Z
    altInput:true,
    altFormat:props.enableTime?"d-m-Y H:i":"d-m-Y",
    clickOpens: true,
    enableSeconds: false,
    minuteIncrement: 1,
    mode:"single"
  })

  const localVal = computed({
    get() {
      return moment.utc(props.value).toDate();
    },

    set(value1) {
    }
  })

  const onDateChange = (selectedDates, dateStr, instance) => {
    if(props.enableTime) {
      emit('update:value', moment(selectedDates[0]).utc().format('YYYY-MM-DDTHH:mm:ss') + "Z")
    }
    else
    {
      if(props.endOfDayTime)
        emit('update:value', moment(selectedDates[0]).endOf('day').utc().format('YYYY-MM-DDTHH:mm:ss')+ "Z")
      else
        emit('update:value', moment(selectedDates[0]).startOf('day').utc().format('YYYY-MM-DDTHH:mm:ss')+"Z")
    }

    if(props.enableTime) {
      emit('onChange', moment(selectedDates[0]).utc().toDate());
    }else{
      if(props.endOfDayTime)
        emit('onChange', moment(selectedDates[0]).endOf('day').utc().toDate());
      else
        emit('onChange', moment(selectedDates[0]).startOf('day').utc().toDate());
    }
  }

  const onClose = (selectedDates, dateStr, instance) => {
    if(props.enableTime) {
      emit('update:value', moment(selectedDates[0]).utc().format('YYYY-MM-DDTHH:mm:ss') + "Z")
    }
    else
    {
      if(props.endOfDayTime)
        emit('update:value', moment(selectedDates[0]).endOf('day').utc().format('YYYY-MM-DDTHH:mm:ss')+ "Z")
      else
        emit('update:value', moment(selectedDates[0]).startOf('day').utc().format('YYYY-MM-DDTHH:mm:ss')+"Z")
    }
  }

  onMounted(() => {
  })
</script>

<template>
  <div :style='[(w == "" ? "" : "width:" + w)]'
    :class='[ "flex w-auto mb-2", (alignHozirontal ? "flex-row justify-start items-center" : "flex-col")]'>

    <label :style='[w_label == "" ? "" : ("width:" + w_label)]'
      class="mr-2 block text-sm  font-medium text-white">{{ label }}</label>

    <div class="bg-inputBg text-inputText border border-gray-500 text-sm rounded-md block p-2 
                focus:ring-gray-200 border-gray-500 flex items-center">
      <flat-pickr v-model="localVal" :disabled="disabled" @onChange="onDateChange" 
          @onClose="onClose" :config="config" placeholder="Select date" class="bg-inputBg text-inputText text-sm p-0 border-0 grow border-transparent focus:border-transparent focus:ring-0   ">
      </flat-pickr>
