<script setup>
import { computed,defineProps, defineEmits,ref } from 'vue'

  const props = defineProps({
    label: String,
    value: String,
    error: String,

    mandatory: {
      type: Boolean,
      default: false
    },
    isNumber: {
      type: Boolean,
      default: false
    },
    w_label: {
      type: String,
      default: ""
    },
    w: {
      type: String,
      default: ""
    },
    w_value: {
      type: String,
      default: ""
    },
    inputAdditionalClass: {
      type: String,
      default: ""
    },
    disabled: {
      type: Boolean,
      default: false
    },
    alignHozirontal: {
      type: Boolean,
      default: false
    },
    rows: {
      type: String,
      default:10
    },
    cols: {
      type: String,
      default:50
    }
  })


  const emit = defineEmits(['update:value'])


  const localVal = computed({
    get() {
      return props.value        
    },

    set(value1) {
      emit('update:value', value1)
    }
  })

  
  const validateKeypress = (evt) => {
    const el = evt.target;
    var sel = el.selectionStart;
    sel++;

    // setTimeout(() => {
    //   el.setSelectionRange(sel, sel);
    // },1)

    if (evt.keyCode === 13)
    {
        const currentInput = event.target;

        // Determine the index of the current input
        const currentIndex = ref.findIndex(ref => ref === currentInput);

        // Focus on the next input element if available
        if (ref[currentIndex + 1]) {
          ref[currentIndex + 1].focus();
        }
    }

    if(props.isNumber){
      return isNumber(evt);
    }
  }

  const isNumber = (evt) => {
    evt = (evt) ? evt : window.event;
    var charCode = (evt.which) ? evt.which : evt.keyCode;
    if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
      evt.preventDefault();;
    } else {
      return true;
    }
  }

  
  var changeTimer;
  const onDataChange = (data) => {
    clearTimeout(changeTimer);
    
    changeTimer = setTimeout(() => {
      emit('onChange', localVal);
    },1000)    
  }
</script>

<template>
  <div :style='[(w==""?"":"width:"+w)]' :class='["flex w-auto mb-2", (alignHozirontal?"flex-row justify-start items-center":"flex-col")]' >

    <label :style='[w_label==""?"":("width:" + w_label)]'  
          class="mr-2 block text-sm font-medium text-white">{{label}}</label>

    <textarea :disabled="disabled" :readonly="disabled"  v-model="localVal" :rows="rows" :cols="cols"
           @keypress="validateKeypress($event)" @input="onDataChange"
           :style='[(w_value=="" ? "":"width:"+w_value)]'
           :class='[inputAdditionalClass,disabled?"bg-inputDisable":"bg-inputBg",  " text-inputText hover:bg-inputHover border-5 focus:bg-inputHover disabled:bg-inputBgDisable disabled:text-inputText text-sm rounded-md block p-2", (alignHozirontal ? "" : "w-full")]'/>
  </div>
  
</template>

