<script setup>
    import { inject, ref, computed } from 'vue'
    const auth = inject('auth');

    const accessMenusRaw = ref([
    {
        id: "home",
        module: "home",
        path: "/home",
        name: "Home"
    },
    {
        id: "trans",
        module: "Transaction",
        name: "Transaction",
        submenu :[
            {
                id: "ticket",
                module: "Transaction",
                path: "/ticket",
                name: "Ticket",
                permissions:["TicketView","TicketCreate","TicketUpdate","TicketDelete"]
            },
            {
                id: "cargo",
                module: "Transaction",
                path: "/cargomanifest",
                name: "Cargo Manifest",
                permissions:["ManifestView","ManifestCreateUpdate","ManifestDelete"]
            }
        ]
    },
    {
        id: "master",
        module: "Master",
        name: "Master",
        submenu :[
            {
                id: "masteruser",
                module: "Master",
                path: "/master_user",
                name: "Master User",
                permissions:["MasterUserView","MasterUserManage"]
            },
            {
                id: "mastergolongan",
                module: "Master",
                path: "/master_golongan",
                name: "Master Golongan",
                permissions:["MasterGolonganView","MasterGolonganManage"]
            },
            {
                id: "masterplat",
                module: "Master",
                path: "/master_golongan_plat",
                name: "Master Golongan Plat",
                permissions:["MasterPlatView","MasterPlatManage"]
            }
        ]
    },
    {
        id: "report",
        module: "Report",
        name: "Monitoring",
        submenu :[
            {
                id: "report_cargo",
                module: "Report",
                path: "/report_cargo",
                name: "Report Cargo",
                permissions:["Report"]
            },
            /*{
                id: "report_pendapatan",
                module: "Report",
                path: "/report_pendapatan",
                name: "Pendapatan",
                permissions:["Report"]
            },
            {
                id: "report_komposisi_perkapal",
                module: "Report",
                path: "/report_komposisi_perkapal",
                name: "Komposisi Per Kapal",
                permissions:["Report"]
            },
            {
                id: "report_komposisi_allkapal",
                module: "Report",
                path: "/report_komposisi_allkapal",
                name: "Komposisi Semua kapal",
                permissions:["Report"]
            },
            {
                id: "report_tripkapal",
                module: "Report",
                path: "/report_tripkapal",
                name: "Trip Kapal",
                permissions:["Report"]
            },
            {
                id: "report_plat",
                module: "Report",
                path: "/report_plat",
                name: "Cari Data Plat Kendaraan",
                permissions:["Report"]
            },
            {
                id: "report_pengurus",
                module: "Report",
                path: "/report_pengurus",
                name: "Pengurus",
                permissions:["Report"]
            },
            {
                id: "report_ukuran_kend",
                module: "Report",
                path: "/report_ukuran_kend",
                name: "Ukuran Kendaraan",
                permissions:["Report"]
            },
            {
                id: "report_penggunaan_app",
                module: "Report",
                path: "/report_penggunaan_app",
                name: "Penggunaan Aplikasi",
                permissions:["Report"]
            },
            {
                id: "report_perubahan_data",
                module: "Report",
                path: "/report_perubahan_data",
                name: "Perubahan Data",
                permissions:["Report"]
            }*/
        ]
    }
]);

var r =  auth.getTokenData();

const accessMenus = computed({
    get() {
        var r =  accessMenusRaw.value.filter(x => {
            if(x.submenu){
                x.submenu = x.submenu.filter(y => {
                    return auth.checkPermission(y.permissions);
                });
                return x.submenu.length > 0;
            }else{
                x.submenu = [];
                return true;
            }
        });
        return r;
    },

    set(value1) {

    }
})

const userName = computed({
    get(){
        if(r){
            return r.UserName;
        };
        return "User Name";
    }
})

const userRole = computed({
    get(){
        if(r){
            return r.Role;
        };
        return "User Name";
    }
})
</script>
<template>
    <nav class="bg-gray-900 border-gray-700">
        <div class="flex flex-wrap items-center p-4">

            <a href="/home" class="flex ">
                <img src="@/assets/ship.svg" class="h-8 mr-3" alt="Flowbite Logo" />
                <span class="self-center text-2xl font-semibold whitespace-nowrap text-white">PT. BANDAR NIAGA RAYA</span>
            </a>

            <button data-collapse-toggle="navbar-dropdown" type="button"
                class="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600"
                aria-controls="navbar-dropdown" aria-expanded="false">
                <span class="sr-only">Main Menu</span>
                <svg class="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
                    <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                        d="M1 1h15M1 7h15M1 13h15" />
                </svg>
            </button>


            <div class="grow"></div>

            <div class="hidden w-full md:block md:w-auto justify-self-end" id="navbar-dropdown">
                <ul 
                    class="flex flex-row font-medium p-0 mt-0 border rounded-lg md:space-x-3 rtl:space-x-reverse border-0 bg-gray-800">

                    <li v-for="accessMenu in accessMenus" class="inline-flex items-center px-2 rounded-lg text-white focus:text-white border-gray-600 hover:bg-gray-600"> 

                        <svg xmlns="http://www.w3.org/2000/svg" v-if="accessMenu.id=='home'"
                            viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5 mr-1 ">
                            <path fill-rule="evenodd" d="M9.293 2.293a1 1 0 0 1 1.414 0l7 7A1 1 0 0 1 17 11h-1v6a1 1 0 0 1-1 1h-2a1 1 0 0 1-1-1v-3a1 1 0 0 0-1-1H9a1 1 0 0 0-1 1v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-6H3a1 1 0 0 1-.707-1.707l7-7Z" clip-rule="evenodd" />
                        </svg>

                        <svg xmlns="http://www.w3.org/2000/svg" v-if="accessMenu.id!='home'" 
                            viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5 mr-1">
                            <path d="M10.362 1.093a.75.75 0 0 0-.724 0L2.523 5.018 10 9.143l7.477-4.125-7.115-3.925ZM18 6.443l-7.25 4v8.25l6.862-3.786A.75.75 0 0 0 18 14.25V6.443ZM9.25 18.693v-8.25l-7.25-4v7.807a.75.75 0 0 0 .388.657l6.862 3.786Z" />
                        </svg>

                        <a v-if="accessMenu.submenu.length == 0"  
                            :id="accessMenu.id"
                            :href="accessMenu.path"
                            class="block py-2 px-3 text-white bg-blue-700 rounded-lg bg-transparent"
                            aria-current="page">{{accessMenu.name}}</a>

                        <button :id = "accessMenu.id" 
                                :href="accessMenu.path"
                                v-if="accessMenu.submenu.length > 0"
                                :data-dropdown-toggle="accessMenu.id + 'link'" 
                                class="flex items-center h-10 w-full py-0  w-auto">
                                {{accessMenu.name}} 
                                <svg v-if="accessMenu.submenu.length > 0"  
                                      class="w-2.5 h-2.5 ml-2 mt-1" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" 
                                      viewBox="0 0 10 6"><path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="m1 1 4 4 4-4" /></svg>
                        </button>

                        <!-- Dropdown menu -->
                        <div v-if="accessMenu.submenu.length > 0" 
                            :id = "accessMenu.id + 'link'" 
                            data-popper-placement="bottom"
                            class="z-10 hidden font-normal divide-y divide-gray-100 rounded-lg shadow w-44 bg-gray-700 divide-gray-600">
                            
                            <ul class="py-2 text-sm text-gray-700 dark:text-gray-400" aria-labelledby="dropdownLargeButton">
                                <li v-for="submenuItem in accessMenu.submenu">
                                    <a :id = "submenuItem.id" 
                                       :href="submenuItem.path"
                                       class="block px-4 py-2 text-white hover:bg-gray-100 hover:bg-gray-600 hover:text-white">{{submenuItem.name}}</a>
                                </li>
                            </ul>
                        </div>
                    </li>
                  
                  
                </ul>
            </div>

            <button 
                data-dropdown-toggle="profileMenulink" 
                class="text-white flex flex-row rounded-xl py-2 px-4 items-center ml-4  bg-opacity-30 bg-yellow-600">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5">
                    <path d="M10 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6ZM3.465 14.493a1.23 1.23 0 0 0 .41 1.412A9.957 9.957 0 0 0 10 18c2.31 0 4.438-.784 6.131-2.1.43-.333.604-.903.408-1.41a7.002 7.002 0 0 0-13.074.003Z" />
                </svg>
                <label class="text-white ml-2">{{userName}}</label>
            </button>

            <div id = "profileMenulink" 
                data-popper-placement="bottom"
                class="z-10 hidden font-normal divide-y divide-gray-100 rounded-lg shadow w-44 bg-gray-700 divide-gray-600">

                <div class="flex flex-col text-white p-3">
                    <label class="text-size-sm text-blue-400">Role</label>
                    <label class="font-bold text-yellow-300">{{userRole}}</label>
                </div>
                

                <ul class="py-2 text-sm text-gray-700 dark:text-gray-400" aria-labelledby="dropdownLargeButton">
                    <li>
                        <a href="/change_password" 
                        class="block px-4 py-2 text-white hover:bg-gray-100 hover:bg-gray-600 hover:text-white">Change Password</a>
                    </li>
                    <li>
                        <a href="/logout" 
                        class="block px-4 py-2 text-white hover:bg-gray-100 hover:bg-gray-600 hover:text-white">Logout</a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>