<script setup>
  import { onMounted ,onUnmounted ,ref,watch } from 'vue'

    const emit = defineEmits(['onSubmitClick','onCancelClick'])

    const props = defineProps({
      title: String,
      data : {
        type: Object
      },
      closeAble: {
        type: Boolean,
        default: false
      },

      enableSubmit: {
        type: Boolean,
        default: true
      },

      visible: {
        type: Boolean,
        default: false
      },
      w: {
        type: String,
        default: "40%"
      },
      dialogDataChangeCallback:{
        type: Function
      }
    })

    const isVisible = ref(false);

    const currentDialogData = ref(props.data)

/*    watch(currentDialogData, (newVal, oldVal) => {
      if(props.dialogDataChangeCallback)
        props.dialogDataChangeCallback(currentDialogData);
      console.log("currentDialogData " + JSON.stringify(newVal))
    },
    { deep: true });;
*/

    const onSubmitClick = () => {
      emit("onSubmitClick", currentDialogData.value);
    };

    const onCancelClick = () => {
      isVisible.value = false;
      emit("onCancelClick");
    };
    

    const hide = () => {
      isVisible.value = false;
    };

    const showDlg = (data) => {
      currentDialogData.value = JSON.parse(JSON.stringify(data));
      isVisible.value = true;
    };

   

    defineExpose({
      hide, showDlg, currentDialogData
    });
</script>

<template >
  
  <div tabindex="-1" v-if="isVisible" @keydown.esc="hide"
        class="p-2 fixed top-0 right-0 left-0 z-20 flex justify-center items-center h-full w-full md:inset-0 h-[calc(100%-1rem)] max-h-full">
        
    <div :style='[(w==""?"":"width:"+w)]' class="p-2 place-self-center h-full">
      
      <div class="flex flex-col max-h-full rounded-lg shadow bg-gray-700">
      
        <div class="flex items-center justify-between px-5 py-2  border-b rounded-t border-gray-600">
          <h3 class="text-lg font-semibold text-white">
              {{title}}
          </h3>

          <button type="button" class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ms-auto 
                  inline-flex justify-center items-center hover:bg-gray-600 hover:text-white" 
                  @click="hide">
              <svg class="w-3 h-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 14">
                  <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"/>
              </svg>
              <span class="sr-only">Close modal</span>
          </button>
        </div>
        
        <!-- Modal body -->
        <div class="overflow-y-auto overflow-visible">
          <div class="px-5 pt-5">
            <slot name="dlgContent" :currentDialogData="currentDialogData"></slot>
          </div>
        </div>

        <div class="flex flex-row items-center px-3 pb-3">
            <button @click="onSubmitClick" v-if="enableSubmit" style="height:3em"
                    class="inline-flex items-center justify-center text-white bg-blue-700 m-2 hover:bg-blue-800 focus:ring-4 
                        focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-3 py-2.5 text-center ">

              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="mr-2 w-5 h-5">
                <path d="M1 4.25a3.733 3.733 0 0 1 2.25-.75h13.5c.844 0 1.623.279 2.25.75A2.25 2.25 0 0 0 16.75 2H3.25A2.25 2.25 0 0 0 1 4.25ZM1 7.25a3.733 3.733 0 0 1 2.25-.75h13.5c.844 0 1.623.279 2.25.75A2.25 2.25 0 0 0 16.75 5H3.25A2.25 2.25 0 0 0 1 7.25ZM7 8a1 1 0 0 1 1 1 2 2 0 1 0 4 0 1 1 0 0 1 1-1h3.75A2.25 2.25 0 0 1 19 10.25v5.5A2.25 2.25 0 0 1 16.75 18H3.25A2.25 2.25 0 0 1 1 15.75v-5.5A2.25 2.25 0 0 1 3.25 8H7Z" />
              </svg>
               Submit
            </button>

            <button @click="onCancelClick" style="height:3em"
                    class="inline-flex items-center justify-center text-white m-2 bg-red-600 hover:bg-red-800 focus:ring-4 
                        focus:outline-none focus:ring-red-300 font-medium rounded-lg text-sm px-3 py-2.5 text-center">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="mr-2 w-5 h-5">
                  <path fill-rule="evenodd" d="M10 18a8 8 0 1 0 0-16 8 8 0 0 0 0 16ZM8.28 7.22a.75.75 0 0 0-1.06 1.06L8.94 10l-1.72 1.72a.75.75 0 1 0 1.06 1.06L10 11.06l1.72 1.72a.75.75 0 1 0 1.06-1.06L11.06 10l1.72-1.72a.75.75 0 0 0-1.06-1.06L10 8.94 8.28 7.22Z" clip-rule="evenodd" />
                </svg>

                Cancel
            </button>

            <slot name="additionalButton" :isVisible="editVisible" :currentDialogData="currentDialogData" > </slot>            
        </div>
      </div> 
    </div>
  </div>
</template>

