<script setup>
  import { getCurrentInstance,defineEmits,computed, ref, onMounted, watch } from 'vue'
  import { useRoute,useRouter  } from 'vue-router'

  const emit = defineEmits(['onDelete','onCreateNew','onEdit','onView'])

  const app = getCurrentInstance().appContext;
  const route = useRoute()
  const router = useRouter()

  const props = defineProps({
    title: String,
    data : {
      type: Object
    },
    closeAble: {
      type: Boolean,
      default: false
    },
    enableDelete: {
      type: Boolean,
      default: false,
    },
    enableCreate: {
      type: Boolean,
      default: false,
    },
    limit: {
      type: Number,
      default:10
    },
    visible: {
      type: Boolean,
      default: false
    },
    fields : {
      type: Array
    },
    loadDataFunction: Function
  })
  
  
  const selectionArray1 = [10,20,30];

  const localFields = ref(props.fields);

  const tableData = ref({
    page: 1,
    total: 0,
    search:"",
    sortBy:"",
    sortDesc:""
  });

  const searchText = ref("")

  const limitLocal = ref(props.limit)

  const tableDataRec = ref([]);

  watch(tableData, (newVal,oldVal) => {
    loadData();
  });

  
  watch(limitLocal, (newVal,oldVal) => {
    loadData();
  });


  var searchtimer;
  watch(searchText, (newVal,oldVal) => {
    clearTimeout(searchtimer);
    searchtimer = setTimeout(() => {
      tableData.value.page = 1;
      //limitLocal.value = 1;
      loadData();
    },500)    
  });


  const onDelete =async (data) => {
    var result = await app.config.globalProperties.$showConfirmationDlg("Delete data?","Apakah Anda Yakin Untuk menghapus data","Yes","Cancel");
    if(result)
      emit('onDelete',data);
  }

  const loadData = async() => {
    if(props.loadDataFunction) {
      var datalocal = await props.loadDataFunction(tableData.value.page, tableData.value.sortBy, tableData.value.sortDesc, limitLocal.value, searchText.value);
      tableDataRec.value = datalocal;

      router.replace({ query: { ...route.query, p: tableData.value.page, sb: tableData.value.sortBy, sd: tableData.value.sortDesc, 
              l: limitLocal.value, s: searchText.value } }).catch(() => { });

      if(datalocal !=null && datalocal.length == tableData.value.limit){
        return true;
      }

      return false;
    }
  }



  const onNextPage = () => {
    if(tableDataRec.value.length >= limitLocal.value ){
      tableData.value.page = tableData.value.page  + 1;    
      if(loadData()){
        //gk ada data lagi atau load data gagal
        //tableData.value.page = tableData.value.page  - 1;
      }
   }
  }

  const onPrevPage = () => {
    if(tableData.value.page>1){
      tableData.value.page = tableData.value.page  - 1;
      if(loadData()){
        //gk ada data lagi atau load data gagal
        //tableData.value.page = tableData.value.page  + 1;
      }
    }
  }


  onMounted(() => {
    for(var field of localFields.value) {
      if(field.key == 'action'){
        field.tdClass = 'text-center';
        field.thClass = 'text-center columnActionStyle';
      } 
      else
        field.thClass = 'text-center';

      if(field.date){
        field.formatter= (value) => {
          if(Boolean(value))
            return moment(value).format('DD/MM/YYYY');
          else
            return "";
        }
      } else if (field.number){
        field.formatter= (value) => {
          if(Boolean(value)){
            let val = (value/1).toFixed(0).replace('.', ',')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
          }
          else
            return "";
        }
      } else if (field.datetime){
          field.formatter= (value) => {
          if(Boolean(value))
            return moment.utc(value).local().format('DD/MM/YYYY hh:mm:ss');
          else
            return "";
        }
      }else{
          field.formatter= (value) => {
              return value;
          }
      }
    }

    tableData.value.page = toInteger(route.query.p ?? tableData.value.page) ;
    tableData.value.sortBy = route.query.sb ?? tableData.value.sortBy;
    tableData.value.sortDesc = route.query.sd ?? tableData.value.sortDesc;
    limitLocal.value = toInteger(route.query.l??limitLocal.value)
    searchText.value = route.query.s ?? tableData.value.search;
    loadData();
  })
 
  const onCreateNew = ()=>{
    emit('onCreateNew');
  }

  const onEdit = (data)=>{ 
    emit('onEdit',data);
  }

  const onView = (data)=>{ 
    emit('onView',data);
  }
  
  defineExpose({
    loadData
  });
</script>

<template>
    <div class="flex flex-col overflow-x-auto h-full shadow-md p-2">
        <div class="flex flex-column items-center  justify-end space-y-4 md:space-y-0 pb-4">
          <input v-model="searchText" style="width: 25%;"
                class="input rounded-md bg-gray-600 border-gray-900 placeholder-gray-800 text-white focus:ring-blue-500 py-2 px-2"
                placeholder="Search">

          <button @click="onCreateNew()" v-if="enableCreate"
            class="ml-2 w-8 h-8 inline-flex items-center justify-center border focus:ring-gray-200 font-medium rounded-lg text-sm px-1 py-1.5 bg-gray-600 text-gray-400 border-gray-600 hover:bg-gray-700 hover:border-gray-800 focus:ring-gray-700"
            type="button">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5">
                <path
                    d="M10.75 4.75a.75.75 0 00-1.5 0v4.5h-4.5a.75.75 0 000 1.5h4.5v4.5a.75.75 0 001.5 0v-4.5h4.5a.75.75 0 000-1.5h-4.5v-4.5z" />
            </svg>
          </button>

          <div class="grow"></div>


          <DropDown :data="selectionArray1" v-model:selectedData="limitLocal"></DropDown>
          

          <div class="w-5"></div>
          <div class="flex flex-row">      
            <button @click="onPrevPage" 
                    class="text-white focus:outline-none w-40 mr-2 font-medium rounded-full text-sm px-5 py-2 text-center me-2 mb-2 
                           bg-blue-700  hover:bg-blue-500 focus:ring-blue-800">Prev</button>
      
            <button @click="onNextPage" 
                    class="text-white focus:outline-none w-40 mr-2 font-medium rounded-full text-sm px-5 py-2 text-center me-2 mb-2 
                           bg-blue-700 hover:bg-blue-500 focus:ring-blue-800">Next</button>
          </div>

          
        </div>

        <div class="grow overflow-x-auto overflow-y-auto">
          <table class="overflow-y-auto w-full z-1 text-sm text-left rtl:text-right text-gray-400">
              <thead class="text-xs sticky top-0 uppercase bg-gray-700 text-gray-400">
                  <tr>
                      <th v-for="field in localFields" scope="col" class="px-6 py-3">
                          {{field.label}}
                      </th>

                      <th scope="col" class="pl-2 py-2" v-if="enableDelete">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="red" class="w-5 h-5">
                            <path fill-rule="evenodd"
                                d="M8.75 1A2.75 2.75 0 006 3.75v.443c-.795.077-1.584.176-2.365.298a.75.75 0 10.23 1.482l.149-.022.841 10.518A2.75 2.75 0 007.596 19h4.807a2.75 2.75 0 002.742-2.53l.841-10.52.149.023a.75.75 0 00.23-1.482A41.03 41.03 0 0014 4.193V3.75A2.75 2.75 0 0011.25 1h-2.5zM10 4c.84 0 1.673.025 2.5.075V3.75c0-.69-.56-1.25-1.25-1.25h-2.5c-.69 0-1.25.56-1.25 1.25v.325C8.327 4.025 9.16 4 10 4zM8.58 7.72a.75.75 0 00-1.5.06l.3 7.5a.75.75 0 101.5-.06l-.3-7.5zm4.34.06a.75.75 0 10-1.5-.06l-.3 7.5a.75.75 0 101.5.06l.3-7.5z"
                                clip-rule="evenodd" />
                        </svg>
                      </th>
                  </tr>
              </thead>

              <tbody>
                  <tr v-for="(item, index) in tableDataRec"
                      class="border-b bg-gray-900 border-gray-700 hover:bg-gray-800">
                      <th v-for="(field, index) in localFields" scope="row" @click="onEdit(item)" class="px-4 py-3"
                          :class="{ 'font-medium whitespace-nowrap text-white': field.highlight }">
                          {{ field.formatter(field.key.split('.').reduce((p,c)=>p&&p[c]||null, item)) }}
                          <!-- item[field.key] -->
                      </th>

                      <th scope="row" class="h-full" v-if="enableDelete">                         
                          <button class="px-2" @click="onDelete(item)" >
                              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-5 h-5">
                                  <path fill-rule="evenodd"
                                      d="M8.75 1A2.75 2.75 0 006 3.75v.443c-.795.077-1.584.176-2.365.298a.75.75 0 10.23 1.482l.149-.022.841 10.518A2.75 2.75 0 007.596 19h4.807a2.75 2.75 0 002.742-2.53l.841-10.52.149.023a.75.75 0 00.23-1.482A41.03 41.03 0 0014 4.193V3.75A2.75 2.75 0 0011.25 1h-2.5zM10 4c.84 0 1.673.025 2.5.075V3.75c0-.69-.56-1.25-1.25-1.25h-2.5c-.69 0-1.25.56-1.25 1.25v.325C8.327 4.025 9.16 4 10 4zM8.58 7.72a.75.75 0 00-1.5.06l.3 7.5a.75.75 0 101.5-.06l-.3-7.5zm4.34.06a.75.75 0 10-1.5-.06l-.3 7.5a.75.75 0 101.5.06l.3-7.5z"
                                      clip-rule="evenodd" />
                              </svg>
                          </button>
                      </th>
                  </tr>

              </tbody>
          </table>
        </div>
        <slot name="formEdit" :isVisible="editVisible" > </slot>
    </div>
</template>

<script>
  import moment from 'moment';
  import { toInteger } from 'lodash';

  export default {
    props: {
      actionCreate: {
        default: true,
        type: Boolean
      },
      actionView: {
        default: false,
        type: Boolean
      },
      actionDelete: {
        default: true,
        type: Boolean
      },
      actionEdit: {
        default: true,
        type: Boolean
      },
      

      title : {
        type: String
      },
      search :{
        type: String
      },
      createTitle :{
        type: String,
        default:"CREATE"
      }
      
    },

    slots: ['formEdit'],

    data () {
      return {
        loading: false,
        sortBy: null,
        sortDesc: false,
        editVisible: false,
        currentData:{}
      }
    },

    
  }
</script>
