<script setup>
import { computed, defineComponent, defineEmits,ref,inject,provide  } from 'vue'


const props = defineProps({
  label: String,
  value: Number,
  error: String,

  mandatory: {
    type: Boolean,
    default: false
  },
  min: {
    type: Number,
    default: 0
  },
  max: {
    type: Number,
    default: 9999999999
  },
  w_label: {
    type: String,
    default: ""
  },
  w_value: {
    type: String,
    default: ""
  },
  w: {
    type: String,
    default: ""
  },
  nextElement: {
    type: String,
    default: ""
  },
  disabled: {
    type: Boolean,
    default: false
  },
  alignHozirontal: {
    type: Boolean,
    default: false
  },
  inputAdditionalClass: {
    type: String,
    default: ""
  }
})

const emit = defineEmits(['update:value'])


const localVal = computed({
    get() {
      if(props.value)
        return props.value.toString();
      else 
       return "";
    },
 
    set(newVal) {
      emit('update:value', parseFloat(newVal.toString().replaceAll(",","")));
    }
})


const validateKeypress = (evt) => {
    const el = evt.target;
   
    if (evt.keyCode === 13)
    {
      document.querySelectorAll('input').forEach( el => {
            console.log(el)
            el.addEventListener('keydown', e => {
                console.log(e.keyCode);
                if(e.keyCode === 13) {
                    let nextEl = el.nextElementSibling;
                    console.log(nextEl)
                    if(nextEl.nodeName === 'INPUT') {
                        nextEl.focus();
                    }else {
                        alert('done');
                    }
                }
            })
        })

    }
  }

  
  
  var changeTimer;
  const onDataChange = (data) => {
    clearTimeout(changeTimer);

    changeTimer = setTimeout(() => {
      emit('onChange', localVal);
    },1000)    
  }


  const config = ref({
    "decimal": ".",
    "separator": ",",
    "prefix": "",
    "suffix": "",
    "precision": 2,
    "nullValue": "",
    "masked": false,
    "prefill": true,
    "reverseFill": false,
    "min": props.min,
    "max": props.max
  })
</script>

<template>
  
  <div :style='[(w=="" ? "":"width:"+w)]' :class='["flex mb-2", (alignHozirontal ? "flex-row justify-start items-center" : "flex-col")]'>
    <label :style='[(w_label=="" ? "":("width:" + w_label))]' class=" block text-sm font-medium text-white">{{ label }}</label>

    <input :style='[(w_value =="" ? "":("width:" + w_value))]' :disabled="disabled" :readonly="disabled"  v-number="config" v-model.lazy="localVal" 
          @keypress="validateKeypress($event)" @input="onDataChange"
           :class='[inputAdditionalClass,disabled?"bg-inputDisable":"bg-inputBg",  " text-inputText hover:bg-inputHover border-5 focus:bg-inputHover disabled:bg-inputBgDisable disabled:text-inputText text-sm rounded-md block p-2", (alignHozirontal ? "" : "w-full")]'>
  </div>
</template>
