
import Vue from 'vue'
import { createApp } from 'vue'
import { createPinia } from 'pinia'
import './index.css'
import 'flowbite';
import axios from 'axios'
import VueNumberFormat from '@coders-tm/vue-number-format'


import App from './App.vue'
import router from './router'
import NavbarMenu from './components/NavbarMenu.vue'
import PageLoadingSpinner from './components/PageLoadingSpinner.vue'
import DataTable from './components/DataTable.vue'
import MenuTitleLabel from './components/MenuTitleLabel.vue'
import MyInputText from './components/MyInputText.vue'
import MyInputNumber from './components/MyInputNumber.vue'
import InputModalDialog from './components/InputModalDialog.vue'
import AlertMessageBox from './components/AlertMessageBox.vue'
import DropDown from './components/DropDown.vue'
import MyInputDate from './components/MyInputDate.vue'
import MyInputTextArea from './components/MyInputTextArea.vue'
import VueMultiselect from 'vue-multiselect'

const appconfig = {
  APIBaseUrl: "http://localhost:8080"
}


const client = axios.create({
    baseURL: appconfig.APIBaseUrl,
    json: true
})


const app = createApp(App)
app.component("NavbarMenu",NavbarMenu)
app.component("PageLoadingSpinner",PageLoadingSpinner)
app.component("DataTable",DataTable)
app.component("MenuTitleLabel",MenuTitleLabel)
app.component("MyInputText",MyInputText)
app.component("MyInputNumber",MyInputNumber)
app.component("InputModalDialog",InputModalDialog)
app.component("AlertMessageBox",AlertMessageBox)
app.component("DropDown",DropDown)
app.component("MyInputDate",MyInputDate)
app.component("MyInputTextArea",MyInputTextArea)
app.component('VueMultiselect', VueMultiselect)

app.config.globalProperties.$showLoading= false;

app.config.globalProperties.$modal = {
  msgBoxConfirm(message){
    let token = localStorage.getItem('token');
    return token;
  }
}

var auth = {
    getToken(){
      let token = localStorage.getItem('token');
      return token;
    },
  
    getTokenData(){
      let token = localStorage.getItem('token');
  
      if (token) 
        return JSON.parse(atob(token.split('.')[1]));
      
      return null;
    },
  
    setToken(token){
      localStorage.setItem( 'token', token);
    },

    hasToken(){
      let token = localStorage.getItem('token');
      if(token != null){
        return true;
      }
      return false;
    },
  
    setAccess(accessObj){
      localStorage.setItem( 'access', JSON.stringify(accessObj));
    },
  
    getAccess(key){
      let access = localStorage.getItem('access');
  
      if (access) {
        var obj =  JSON.parse(access);
        return obj[key];
      }
    },
  
    removeToken(){
      localStorage.removeItem("token");
    },

    checkPermission(permission){
      var tokenData = this.getTokenData();
      if(permission) {
        const filteredArray = tokenData.permission.split(",").filter(value => permission.includes(value));
        return filteredArray.length > 0;
      }
      return true;
    },

    //local user data
    setLocalUser(user,pass){
      localStorage.setItem( 'localuser', JSON.stringify({
        user:user,
        pass:pass
      }));
    },

    getLocalUser(){
      let localUser = localStorage.getItem('localuser');
      if (localUser) {
        var obj =  JSON.parse(localUser);
        return obj;
      }
    },
    removeLocalUser() {
      localStorage.removeItem("localuser");
    }

  }


var httpClient = async (method, resource, data, responseType="json")  => {
    // inject the accessToken for each request
    let accessToken = auth.getToken();
    return client({
      method,
      url: resource,
      data,
      responseType:responseType, 
      headers: {
        Authorization: `${accessToken}`
      }
    }).then(req => {
      return req;
    }).catch(async e => {
      var errMsg = "";
      console.log(e);
      if (e.response) {
        
        if (e.response.status === 401) {
          setTimeout(() => {
            auth.removeToken();
            router.push('login');  
          },1000);
        }

        errMsg = e.toString();
        if(Boolean(e.response) && Boolean(e.response.data)) {
          if (
              e.request.responseType === 'blob' &&
              e.response.data instanceof Blob &&
              e.response.data.type &&
              e.response.data.type.toLowerCase().indexOf('json') != -1
          ) {
            errMsg = JSON.parse(await e.response.data.text()).message;
          }else{
            if(Boolean(e.response.data.message))
              errMsg = e.response.data.message;
          }
        }
  
        return {
          "is_ok":false,"message":errMsg,"data":null
        }
      } else if (e.request) {
        errMsg = "Network error " + e.toString()
        return {
          "is_ok":false,"message":errMsg,"data":null
        }
      } else {
  
      }
    });
  };
app.config.globalProperties.$httpClient = httpClient;




app.provide('httpClient', httpClient);
app.provide('auth', auth);
app.provide('appconfig', appconfig);

Vue.use(VueNumberFormat, { precision: 4 })


app.use(createPinia())
app.use(router)

app.mount('#app')
