<script setup>
  import { onMounted } from 'vue'
  import { initFlowbite } from 'flowbite'

import { ref, watch,getCurrentInstance, inject, toRef   } from 'vue'

  const app  = getCurrentInstance().appContext;
  const auth = inject('auth');

  const loading = ref(false);
  const showNav = ref(false);

  const dialogMessage = ref("");
  const dialogTitle = ref("");
  const dialogCallback = ref(null);
  const okButtonLocalLabel = ref("");
  const cancelButtonLocalLabel = ref("");
  const confirmationDlgRef = ref(null)

  const listPermissionCode = {
    TicketView:"TicketView",
    TicketCreate:"TicketCreate",
    TicketUpdate:"TicketUpdate",
    TicketDelete:"TicketDelete",
    ManifestView:"ManifestView",
    ManifestCreateUpdate:"ManifestCreateUpdate",
    ManifestDelete:"ManifestDelete",
    MasterGolonganView:"MasterGolonganView",
    MasterGolonganManage:"MasterGolonganManage",
    MasterPlatView:"MasterPlatView",
    MasterPlatManage:"MasterPlatManage",
    MasterUserView:"MasterUserView",
    MasterUserManage:"MasterUserManage"
  };


  onMounted(() => {
      initFlowbite();
      console.log("initFlowbite");
      if(auth.hasToken())
        showNav.value = true;
  })
  

  const showLoading = (show)=> {
    loading.value = show;
  }

  const loggedIn = ()=> {
    showNav.value = true;
  }

  const loggedOut = ()=> {
    showNav.value = false;
  }


  // On page load or when changing themes, best to add inline in `head` to avoid FOUC
//  if (localStorage.getItem('color-theme') === 'dark' || (!('color-theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
//    document.documentElement.classList.add('dark');
//  } else {
//      document.documentElement.classList.remove('dark');
//  }

  
  var promiseResolve, promiseReject;

  app.config.globalProperties.$showConfirmationDlg = (title, message, okButtonLabel, cancelButtonLabel) => {
    dialogTitle.value = title;
    dialogMessage.value = message;
    okButtonLocalLabel.value = okButtonLabel;
    cancelButtonLocalLabel.value = cancelButtonLabel;

    setTimeout(() => {
      confirmationDlgRef.value.show();
    },10)

    var promise = new Promise(function(resolve, reject){
      promiseResolve = resolve;
      promiseReject = reject;
    });   
    
    return promise;
  };

  app.config.globalProperties.showInfoDlg = (message) => {
    dialogTitle.value = "Info";
    dialogMessage.value = message;
    confirmationDlgRef.value.headerColor = "green";
    okButtonLocalLabel.value = "OK";
    cancelButtonLocalLabel.value = null;

    setTimeout(() => {
      confirmationDlgRef.value.show();
    },10)
  };

  app.config.globalProperties.showErrorDlg = (message) => {
    dialogTitle.value = "Error";
    confirmationDlgRef.value.headerColor = "red";
    dialogMessage.value = message;
    okButtonLocalLabel.value = "OK";
    cancelButtonLocalLabel.value = null; 

    setTimeout(() => {
      confirmationDlgRef.value.show();
    },10)
  };

  const onDialogOk = () => {
    if(promiseResolve)
      promiseResolve(true);
  }
  var onDialogCancel =() => {
    if(promiseResolve)
      promiseResolve(false);
  }
  var onDialogHide =() => {
    if(promiseResolve)
      promiseResolve(false);
  }
  var onDialogShow =() => {
  }

  defineExpose({
    loading, showNav,showLoading,loggedIn
  });
</script>


<template>
  <div class="fixed top-0 bottom-0 right-0 left-0 z-0 bg-gray-800">

    <div class="flex flex-col absolute top-0 bottom-0 right-0 left-0 ">
      <NavbarMenu v-if="showNav" class="grow-0" />
      <RouterView @showLoading="showLoading" @loggedIn="loggedIn" @loggedOut="loggedOut" 
        class="grow" />
    </div>

    <AlertMessageBox ref="confirmationDlgRef"  :title="dialogTitle" :message="dialogMessage"  
                     :lblOk="okButtonLocalLabel" :lblCancel="cancelButtonLocalLabel" 
                     @onOk="onDialogOk" @onCancel="onDialogCancel" @onHide="onDialogHide" @onShow="onDialogShow"
                     :callback="dialogCallback" class="absolute top-0 bottom-0 right-0 left-0" />

    <PageLoadingSpinner v-if="loading " class="absolute top-0 bottom-0 right-0 left-0 " />

  </div>
</template>

<style>
  .rounded_input {
    border-radius: 5px !important;
    border-top-left-radius: 5px !important;
    border-top-right-radius: 5px !important;
    border-bottom-right-radius: 5px !important;
    border-bottom-left-radius: 5px !important;
  }
</style>